
type PrivacyProps = {
  company: string,
  website: string,
  email: string
}

const PrivacyPolicy = ({ company, website, email }: PrivacyProps) => <>
  <h3>1. WHAT INFORMATION DO WE COLLECT?</h3>
  <h6>Personal information you disclose to us</h6>
  <p><em>In Short: We collect information that you provide to us.</em></p>
  <p>{company} collects personal information that you voluntarily provide to us when you register on the Website, express an interest in obtaining information about us or our products and services, when you register to use {company} products and services or otherwise when you contact us.</p>
  <p>The personal information we collect may include the following:</p>
  <ul>
    <li>Names</li>
    <li>Phone numbers</li>
    <li>Email addresses</li>
    <li>Mailing addresses</li>
    <li>Student identification numbers</li>
    <li>Usernames</li>
    <li>Passwords</li>
  </ul>
  <p>All personal information that you provide to us must be true, complete and accurate, and you must notify us of any changes to such personal information.</p>
  <p><strong>Information automatically collected</strong></p>
  <p><em>In Short:&nbsp; Some information &mdash; such as your IP address and browser characteristics &mdash; is collected automatically.</em></p>
  <p>We automatically collect certain information when you visit, use or navigate to our website. This information does not reveal your specific identity (like your name or contact information) but may include device and usage information, such as your IP address, browser and device characteristics, operating system, language preferences, referring URLs, device name, country, location, information about who and when you use our website and other technical information. This information is primarily needed to maintain the security and operation of our website, and for our internal analytics and reporting purposes.</p>
  <p>Like many businesses, we also collect information through cookies and similar technologies. The information we collect includes:</p>
  <ul>
    <li><em>Log and Usage Data.</em> Log and usage data is service-related, diagnostic usage and performance information our servers automatically collect when you access or use our Website and which we record in log files. Depending on how you interact with us, this log data may include your IP address, device information, browser type and settings and information about your activity in the Website (such as the date/time stamps associated with your usage, pages and files viewed, searches and other actions you take such as which features you use), device event information (such as system activity, error reports (sometimes called 'crash dumps') and hardware settings).</li>
  </ul>
  <ul>
    <li><em>Location Data.</em> We collect information about your device's location, which can be either precise or imprecise. How much information we collect depends on the type of settings of the device you use to access the Website. For example, we may use GPS and other technologies to collect geolocation data that tells us your current location (based on your IP address). You can opt out of allowing us to collect this information either by refusing access to the information or by disabling your Locations settings on your device. Note however, if you choose to opt out, you may not be able to use certain aspects of the Services.</li>
  </ul>
  <h3>2. HOW DO WE USE YOUR INFORMATION?</h3>
  <p><em>In Short: We process your information for purposes based on legitimate business interests, the fulfillment of our contract with you, compliance with our legal obligations, and/or your consent.</em></p>
  <p>We use personal information collected via our Website for a variety of business purposes described below. We process your personal information for these purposes in reliance on our legitimate business interests, in order to enter into or perform a contract with you, with your consent, and/or for compliance with our legal obligations. We indicate the specific processing grounds we rely on next to each purpose listed below.</p>
  <p>We use the information we collect or receive:</p>
  <ul>
    <li>To facilitate account creation and logon process (Notice: this is a potential feature; it has not been implemented). If you choose to link your account with us to a third-party account (such as your Google account), we use the information you allowed us to collect from those third parties to facilitate account creation and logon process for the performance of the contract. See the section below headed "HOW DO WE HANDLE YOUR SOCIAL LOGINS" for further information.</li>
    <li>To post testimonials. We post testimonials on our Website that may contain personal information. Prior to posting a testimonial, we will obtain your consent to use your name and the consent of the testimonial. If you wish to update, or delete your testimonial, please contact us at {email} and be sure to include your name, testimonial location, and contact information.</li>
    <li>Request feedback. We may use your information to request feedback and to contact you about your use of our Website.</li>
    <li>To enable user-to-user communications. We may use your information in order to enable user-to-user communications with each user's consent.</li>
    <li>To manage user accounts. We may use your information for the purposes of managing our account and keeping it in working order.</li>
    <li>To send administrative information to you. We may use your personal information to send you product, service and new feature information and/or information about changes to our terms, conditions, and policies.</li>
    <li>To protect our Services. We may use your information as part of our efforts to keep our Website safe and secure (for example, for fraud monitoring and prevention).</li>
    <li>To enforce our terms, conditions and policies for business purposes, to comply with legal and regulatory requirements or in connection with our contract.</li>
    <li>To respond to legal requests and prevent harm. If we receive a subpoena or other legal request, we may need to inspect the data we hold to determine how to respond.</li>
    <li>Fulfill and manage your orders. We may use your information to fulfill and manage your orders, payments, returns, and exchanges made through the Website.</li>
    <li>Administer prize draws and competitions. We may use your information to administer prize draws and competitions when you elect to participate in our competitions.</li>
    <li>To deliver and facilitate delivery of services to the user. We may use your information to provide you with the requested service.</li>
    <li>To respond to user inquiries/offer support to users. We may use your information to respond to your inquiries and solve any potential issues you might have with the use of our Services.</li>
    <li>To send you marketing and promotional communications. We and/or our third-party marketing partners may use the personal information you send to us for our marketing purposes, if this is in accordance with your marketing preferences. For example, when expressing an interest in obtaining information about us or our Website, subscribing to marketing or otherwise contacting us, we will collect personal information from you. You can opt-out of our marketing emails at any time (see the "WHAT ARE YOUR PRIVACY RIGHTS" below).</li>
  </ul>
  <h3>3. WILL YOUR INFORMATION BE SHARED WITH ANYONE?</h3>
  <p><em>In Short: </em><em>We only share information with your consent, to comply with laws, to provide you with services, to protect your rights, or to fulfill business obligations.</em></p>
  <p>We may process or share your data that we hold based on the following legal basis:</p>
  <ul>
    <li>Consent: We may process your data if you have given us specific consent to use your personal information for a specific purpose.</li>
    <li>Legitimate Interests: We may process your data when it is reasonably necessary to achieve our legitimate business interests.</li>
    <li>Performance of a Contract: Where we have entered into a contract with you, we may process your personal information to fulfill the terms of our contract.</li>
    <li>Legal Obligations: We may disclose your information where we are legally required to do so in order to comply with applicable law, governmental requests, a judicial proceeding, court order, or legal process, such as in response to a court order or a subpoena (including in response to public authorities to meet national security or law enforcement requirements).</li>
    <li>Vital Interests: We may disclose your information where we believe it is necessary to investigate, prevent, or take action regarding potential violations of our policies, suspected fraud, situations involving potential threats to the safety of any person and illegal activities, or as evidence in litigation in which we are involved.</li>
  </ul>
  <p>More specifically, we may need to process your data or share your personal information in the following situations:</p>
  <ul>
    <li>Business Transfers. We may share or transfer your information in connection with, or during negotiations of, any merger, sale of company assets, financing, or acquisition of all or a portion of our business to another company.</li>
  </ul>
  <ul>
    <li>Vendors, Consultants and Other Third-Party Service Providers. We may share your data with third-party vendors, service providers, contractors or agents who perform services for us or on our behalf and require access to such information to do that work. Examples include: payment processing, data analysis, email delivery, hosting services, and customer service and marketing efforts. We may allow selected third parties to use tracking technology on the Website, which will enable them to collect data on our behalf about how you interact with our Website over time. This information may be used to, among other things, analyze and track data, determine the popularity of certain content, pages or features, and better understand online activity. <strong>We do not share, sell, rent or trade any of your information with third parties for their promotional purposes.</strong></li>
  </ul>
  <ul>
    <li>Affiliates. We may share your information with our affiliates, in which case we will require those affiliates to honor this privacy notice. Affiliates include our parent company and any subsidiaries, joint venture partners or other companies that we control or that are under common control with us.</li>
  </ul>
  <ul>
    <li>Other Users. When you share personal information (for example, by posting comments, contributions or other content to the Website) or otherwise interact with public areas of the Website, such personal information may be viewed by all users and may be publicly made available outside the Website in perpetuity. If you interact with other users of our Website and register for our Website through a social network (such as Facebook), your contacts on the social network will see your name, profile photo, and descriptions of your activity. Similarly, other users will be able to view descriptions of your activity, communicate with you within our Website, and view your profile.</li>
  </ul>
  <h3>4. WHO WILL YOUR INFORMATION BE SHARED WITH?</h3>
  <p><em>In Short: We do not share information with third parties.</em></p>
  <p>We do not share or disclose your information with third parties. If we have processed your data based on your consent and you wish to revoke your consent, please contact us using the contact details provided in the section below titled "HOW CAN YOU CONTACT US ABOUT THIS NOTICE?".</p>
  <h3>5. DO WE USE COOKIES AND OTHER TRACKING TECHNOLOGIES?</h3>
  <p><em>In Short: </em><em>We may use cookies and other tracking technologies to collect and store your information.</em></p>
  <p>We may use cookies and similar tracking technologies (like web beacons and pixels) to access or store information. Specific information about how we use such technologies and how you can refuse certain cookies is set out in our Cookie Notice.</p>
  <h3>6. HOW DO WE HANDLE YOUR SOCIAL LOGINS?</h3>
  <p><em>In Short: </em><em>If you choose to register or log in to our services using a social media account, we may have access to certain information about you.</em></p>
  <p>Our Website offers you the ability to register and login using your third-party social media account details (like your Google login). Where you choose to do this, we will receive certain profile information about you from your social media provider. The profile Information we receive may vary depending on the social media provider concerned, but will often include your name, email address, friends list, profile picture as well as other information you choose to make public on such social media platforms.</p>
  <p>We will use the information we receive only for the purposes that are described in this privacy notice or that are otherwise made clear to you on the relevant Website. Please note that we do not control, and are not responsible for, other uses of your personal information by your third-party social media provider. We recommend that you review their privacy notice to understand how they collect, use and share your personal information, and how you can set your privacy preferences on their sites and apps.</p>
  <h3>7. WHAT IS OUR STANCE ON THIRD-PARTY WEBSITES?</h3>
  <p><em>In Short: </em><em>We are not responsible for the safety of any information that you share with third-party providers are not affiliated with our Website.</em></p>
  <p>The Website may link to other websites, online services or mobile applications. We cannot guarantee the safety and privacy of data you provide to any third parties. Any data collected by third parties is not covered by this privacy notice. We are not responsible for the content or privacy and security practices and policies of any third parties, including other websites, services or applications that may be linked to or from the Website. You should review the policies of such third parties and contact them directly to respond to your questions.</p>
  <h3>8. HOW LONG DO WE KEEP YOUR INFORMATION?</h3>
  <p><em>In Short: </em><em>We keep your information for as long as necessary to fulfill the purposes outlined in this privacy notice unless otherwise required by law.</em></p>
  <p>We will only keep your personal information for as long as it is necessary for the purposes set out in this privacy notice, unless a longer retention period is required or permitted by law (such as tax, accounting or other legal requirements). No purpose in this notice will require us keeping your personal information for longer than the period of time in which users have an account with us.</p>
  <p>When we have no ongoing legitimate business need to process your personal information, we will either delete or anonymize such information, or, if this is not possible (for example, because your personal information has been stored in backup archives), then we will securely store your personal information and isolate it from any further processing until deletion is possible.</p>
  <h3>9. HOW DO WE KEEP YOUR INFORMATION SAFE?</h3>
  <p><em>In Short: </em><em>We aim to protect your personal information through a system of organizational and technical security measures.</em></p>
  <p>We have implemented appropriate technical and organizational security measures designed to protect the security of any personal information we process. However, despite our safeguards and efforts to secure your information, no electronic transmission over the Internet or information storage technology can be guaranteed to be 100% secure, so we cannot promise or guarantee that hackers, cybercriminals, or other unauthorized third parties will not be able to defeat our security, and improperly collect, access, steal, or modify your information. Although we will do our best to protect your personal information, transmission of personal information to and from our Website is at your own risk. You should only access the Website within a secure environment.</p>
  <h3>10. WHAT ARE YOUR PRIVACY RIGHTS?</h3>
  <p><em>In Short: </em><em>You may review, change, or terminate your account at any time.</em></p>
  <p>If you are resident in the European Economic Area and you believe we are unlawfully processing your personal information, you also have the right to complain to your local data protection supervisory authority. You can find their contact details here: <a href="http://ec.europa.eu/justice/data-protection/bodies/authorities/index_en.htm" target="_blank" rel="noopener">http://ec.europa.eu/justice/data-protection/bodies/authorities/index_en.htm</a>.</p>
  <p>If you are resident in Switzerland, the contact details for the data protection authorities are available here: <a href="https://www.edoeb.admin.ch/edoeb/en/home.html" target="_blank" rel="noopener">https://www.edoeb.admin.ch/edoeb/en/home.html</a>.</p>
  <p>If you have questions or comments about your privacy rights, you may email us at {email}.</p>
  <h6>Account Information</h6>
  <p>If you would at any time like to review or change the information in your account or terminate your account, you can:</p>
  <p>&nbsp;&nbsp;&nbsp;&nbsp;■&nbsp; Log in to your account settings and update your user account.</p>
  <p>Upon your request to terminate your account, we will deactivate or delete your account and information from our active databases. However, we may retain some information in our files to prevent fraud, troubleshoot problems, assist with any investigations, enforce our Terms of Use and/or comply with applicable legal requirements.</p>
  <h6>Cookies and similar technologies</h6>
  <p>Most Web browsers are set to accept cookies by default. If you prefer, you can usually choose to set your browser to remove cookies and to reject cookies. If you choose to remove cookies or reject cookies, this could affect certain features or services of our Website. To opt-out of interest-based advertising by advertisers on our Website, visit <a href="http://www.aboutads.info/choices/" target="_blank" rel="noopener">http://www.aboutads.info/choices/</a>.</p>
  <h3>11. CONTROLS FOR DO-NOT-TRACK FEATURES</h3>
  <p>Most web browsers and some mobile operating systems and mobile applications include a Do-Not-Track "DNT" feature or setting you can activate to signal your privacy preference not to have data about your online browsing activities monitored and collected. We honor Do Not Track (DNT) signals. When a DNT signal is present, {company} does not place tracking cookies. However, we still place cookies that are essential for site operation.</p>
  <h3>12. DO CALIFORNIA RESIDENTS HAVE SPECIFIC PRIVACY RIGHTS?</h3>
  <p><em>In Short: </em><em>Yes, if you are a resident of California, you are granted specific rights regarding access to your personal information.</em></p>
  <p>California Civil Code Section 1798.83, also known as the "Shine The Light" law, permits our users who are California residents to request and obtain from us, once a year and free of charge, information about categories of personal information (if any) we disclosed to third parties for direct marketing purposes and the names and addresses of all third parties with which we shared personal information in the immediately preceding calendar year. If you are a California resident and would like to make such a request, please submit your request electronically at {email}.</p>
  <p>If you are under 18 years of age, reside in California, and have a registered account with the Website, you have the right to request removal of unwanted data that you publicly post on the Website. To request removal of such data, please contact us using the contact information provided below, and include the email address associated with your account and a statement that you reside in California. We will make sure the data is not publicly displayed on the Website, but please be aware that the data may not be completely or comprehensively removed from all our systems (e.g. backups, etc.).</p>
  <h6>Your rights with respect to your personal data:</h6>
  <p><strong>Right to request deletion of the data - Request to delete</strong></p>
  <p>You can ask for the deletion of your personal information. If you ask us to delete your personal information, we will respect your request and delete your personal information, subject to certain exceptions provided by law, such as (but not limited to) the exercise by another consumer of his or her right to free speech, our compliance requirements resulting from a legal obligation or any processing that may be required to protect against illegal activities.</p>
  <p><strong>Right to be informed - Request to know</strong></p>
  <p>Depending on the circumstances, you have a right to know:</p>
  <ul>
    <li>whether we collect and use your personal information;</li>
    <li>the categories of personal information that we collect;</li>
    <li>the purposes for which the collected personal information is used;</li>
    <li>whether we sell your personal information to third parties;</li>
    <li>the categories of personal information that we sold or disclosed for a business purpose;</li>
    <li>the categories of third parties to whom the personal information was sold or disclosed for a business purpose; and</li>
    <li>the business or commercial purpose for collecting or selling personal information.</li>
  </ul>
  <p>In accordance with applicable law, we are not obligated to provide or delete consumer information that is de-identified in response to a consumer request or to re-identify individual data to verify a consumer request.</p>
  <h6>Right to Non-Discrimination for the Exercise of a Consumer's Privacy Rights</h6>
  <p>We will not discriminate against you if you exercise your privacy rights.</p>
  <h6>Verification process</h6>
  <p>Upon receiving your request, we will need to verify your identity to determine you are the same person about whom we have the information in our system. These verification efforts require us to ask you to provide information so that we can match it with the information you have previously provided us. For instance, depending on the type of request you submit, we may ask you to provide certain information so that we can match the information you provide with the information we already have on file, or we may contact you through a communication method (e.g. phone or email) that you have previously provided to us. We may also use other verification methods as the circumstances dictate.</p>
  <p>We will only use personal information provided in your request to verify your identity or authority to make the request. To the extent possible, we will avoid requesting additional information from you for the purposes of verification. If, however, we cannot verify your identity from the information already maintained by us, we may request that you provide additional information for the purposes of verifying your identity, and for security or fraud-prevention purposes. We will delete such additionally provided information as soon as we finish verifying you.</p>
  <h6>Other privacy rights</h6>
  <ul>
    <li>you may object to the processing of your personal data</li>
    <li>you may request correction of your personal data if it is incorrect or no longer relevant, or ask to restrict the processing of the data</li>
    <li>you can designate an authorized agent to make a request under the CCPA on your behalf. We may deny a request from an authorized agent that does not submit proof that they have been validly authorized to act on your behalf in accordance with the CCPA.</li>
    <li>you may request to opt-out from future selling of your personal information to third parties. Upon receiving a request to opt-out, we will act upon the request as soon as feasibly possible, but no later than 15 days from the date of the request submission.</li>
  </ul>
  <p>To exercise these rights, you can contact us by email at {email}, or by referring to the contact details at the bottom of this document. If you have a complaint about how we handle your data, we would like to hear from you.</p>
  <h6>Applicable for Children Under 13</h6>
  <p>{company} collects personal information of students who may be under 13. We follow the guidelines of the Children's Online Privacy Protection Act ("COPPA"), U.S. federal law designated to protect online privacy and identities of minors under 13.</p>
  <p>We will obtain parental consent for minors under 13 before student registration and personal information collection, unless the request for personal information is deemed acceptable without consent under COPPA. Parents must agree to the following Privacy Policy as well as {company} Terms of Service and Cookie Policy.</p>
  <p>{company} does not make public personal information collected from any students, nor do we allow students to release personal information when using our products and services. Furthermore, we do not condition a child&rsquo;s participation in an online activity that leads to a child providing more information than is reasonably necessary to participate in that activity.</p>
  <p>{company} works closely with consumer schools and school districts to remain in accordance with COPPA as well as the Children&rsquo;s Internet Protection Act (CIPA), that addresses children&rsquo;s access to harmful Internet content, and Family Educational Rights and Privacy Act (FERPA), that protects student education records. We will ensure that schools and school districts disclose to parents and students upon "directory" information, information that can be released without consent, and information that requires parental consent. Furthermore, {company} ensures that parents and students over 13 understand their privacy rights upon request.</p>
  <p>For any requests to review, update, and/or delete personal information that may have been collected by {company}, parents may contact us at {email}.</p>
  <h3>13. DO WE MAKE UPDATES TO THIS NOTICE?</h3>
  <p><em>In Short: </em><em>Yes, we will update this notice as necessary to stay compliant with relevant laws.</em></p>
  <p>We may update this privacy notice from time to time. The updated version will be indicated by an updated "Revised" date and the updated version will be effective as soon as it is accessible. If we make material changes to this privacy notice, we may notify you either by prominently posting a notice of such changes or by directly sending you a notification. We encourage you to review this privacy notice frequently to be informed of how we are protecting your information.</p>
  <h3>14. HOW CAN YOU CONTACT US ABOUT THIS NOTICE?</h3>
  <p>If you have questions or comments about this notice, you may email us at {email}.</p>
  <h3>HOW CAN YOU REVIEW, UPDATE, OR DELETE THE DATA WE COLLECT FROM YOU?</h3>
  <p>Based on the applicable laws of your country, you may have the right to request access to the personal information we collect from you, change that information, or delete it in some circumstances. To request to review, update, or delete your personal information, please submit a request form contacting us through email at {email}. We will respond to your request within 30 days.</p>
</>

export default PrivacyPolicy;
